.summary {
    padding: 15px 20px;

    .export-section {
        margin-top: 40px;
    }
    
    .export-inputs {
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    
    .export-inputs *,
    .export-inputs input {
        margin: 0;
        margin-right: 6px;
    }

    .deploy-info {
        position: absolute;
        font-size: 10px;
        bottom: .4em;
        right: .8em;
    }
}
